import React from "react";
import "./HistorySidebar.css";

const HistorySidebar = ({ isOpen, onClose, history, onPlayVideo, onRemove, onClear }) => {
  const handleClearAll = () => {
    if (history.length > 0) {
      if (window.confirm("Are you sure you want to clear your watch history?")) {
        onClear();
      }
    }
  };

  return (
    <>
      <div className={`history-overlay ${isOpen ? "open" : ""}`} onClick={onClose}></div>

      <div className={`history-sidebar ${isOpen ? "open" : ""}`}>
        <div className="history-header">
            <div className="header-top">
                <h3>Watch History ({history.length})</h3>
                <button onClick={onClose} className="close-btn"></button>
            </div>
            {history.length > 0 && (
                <button className="clear-all-btn" onClick={handleClearAll}>
                     Clear History
                </button>
            )}
        </div>
        
        <div className="history-content">
            {history.length === 0 ? (
                <div className="empty-msg">
                    <p>No watch history yet.</p>
                </div>
            ) : (
                <ul className="video-list">
                    {history.map((video) => (
                        <li key={video.id} className="video-item" onClick={() => {
                            onPlayVideo(video.id);
                            onClose();
                        }}>
                            <img 
                                src={`https://img.youtube.com/vi/${video.id}/mqdefault.jpg`} 
                                alt="thumb" 
                                className="video-thumb"
                            />
                            <div className="video-info">
                                <span className="video-title">{video.title || "No Title"}</span>
                                <span className="video-cat">{video.category?.replace("videoIds-", "").replace(".json", "") || "Unknown"}</span>
                            </div>
                            
                            <button 
                                className="delete-item-btn" 
                                onClick={(e) => {
                                    e.stopPropagation();
                                    onRemove(video.id);
                                }}
                                title="Remove from history"
                            >
                                
                            </button>
                        </li>
                    ))}
                </ul>
            )}
        </div>
      </div>
    </>
  );
};

export default HistorySidebar;