// src/hooks/useSleepTimer.js
import { useState, useEffect, useRef } from "react";

export const useSleepTimer = (onTimerEnd) => {
  const [timeLeft, setTimeLeft] = useState(0); // Thi gian cn li (giy)
  const [isActive, setIsActive] = useState(false);
  const timerRef = useRef(null);

  useEffect(() => {
    if (isActive && timeLeft > 0) {
      timerRef.current = setInterval(() => {
        setTimeLeft((prev) => prev - 1);
      }, 1000);
    } else if (timeLeft === 0 && isActive) {
      // Ht gi
      setIsActive(false);
      clearInterval(timerRef.current);
      if (onTimerEnd) onTimerEnd(); // Gi hm pause video
    }

    return () => clearInterval(timerRef.current);
  }, [isActive, timeLeft, onTimerEnd]);

  const startTimer = (minutes) => {
    setTimeLeft(minutes * 60);
    setIsActive(true);
  };

  const cancelTimer = () => {
    setIsActive(false);
    setTimeLeft(0);
    if (timerRef.current) clearInterval(timerRef.current);
  };

  // Helper  format hin th (MM:SS)
  const formatTime = () => {
    const m = Math.floor(timeLeft / 60);
    const s = timeLeft % 60;
    return `${m}:${s < 10 ? "0" : ""}${s}`;
  };

  return { timeLeft, isActive, startTimer, cancelTimer, formatTime };
};