import { useEffect } from 'react';
import { driver } from "driver.js";
import "driver.js/dist/driver.css";
import "./TourGuide.css";

// Define the driver object with configuration and steps
const driverObj = driver({
  showProgress: true,
  showButtons: ['next', 'previous'],
  popoverClass: 'driverjs-theme',
  steps: [
    {
      element: '.random-button',
      popover: {
        title: 'Random Button',
        description: `Welcome to RYVP! Press [space-bar] or click this button to random video.`,
        side: 'bottom',
        align: 'center',
      },
    },
    {
      element: '#fileSelector',
      popover: {
        title: 'Subject Selector',
        description: 'Use this dropdown to select a subject.',
        side: 'right',
        align: 'start',
      },
    },
    {
        element: '#youtube-video',
        popover: {
          title: 'Youtube Viewer',
          description: 'You can start/stop/seek/mute... youtube video here',
          side: 'top',
          align: 'start',
        },
      },
    {
      element: '.previous-button',
      popover: {
        title: 'Previous Video',
        description: `Press [ArrowLeft] or click this button to go to the previous video.`,
        side: 'top',
        align: 'center',
      },
    },
    {
      element: '.like-button',
      popover: {
        title: 'Like Button',
        description: 'Click this button to like the current video.',
        side: 'top',
        align: 'center',
      },
    },
    {
      element: '.dislike-button',
      popover: {
        title: 'Dislike Button',
        description: 'Click this button to dislike the current video.',
        side: 'top',
        align: 'center',
      },
    },
    {
      element: '.next-button',
      popover: {
        title: 'Next Video',
        description: `Press [ArrowRight] or click this button to go to the next video.`,
        side: 'top',
        align: 'center',
      },
    },
  ],
});

const TourGuide = () => {
  useEffect(() => {
    const isFirstVisit = localStorage.getItem('firstVisit') === null;

    if (isFirstVisit) {
      // Mark the tour as completed after the first visit
      localStorage.setItem('firstVisit', 'false');

      // Start the tour
      driverObj.drive();
    }
  }, []);

  return null; // This component does not render anything
};

export default TourGuide;
