// src/hooks/useFavorites.js
import { useState, useEffect } from "react";

export const useFavorites = () => {
  const [favorites, setFavorites] = useState(() => {
    try {
      const saved = localStorage.getItem("myFavorites");
      return saved ? JSON.parse(saved) : [];
    } catch {
      return [];
    }
  });

  useEffect(() => {
    localStorage.setItem("myFavorites", JSON.stringify(favorites));
  }, [favorites]);

  const toggleFavorite = (video) => {
    setFavorites((prev) => {
      const exists = prev.find((v) => v.id === video.id);
      if (exists) {
        return prev.filter((v) => v.id !== video.id);
      }
      return [video, ...prev];
    });
  };

  // --- THM MI ---
  const removeFavorite = (videoId) => {
    setFavorites((prev) => prev.filter((v) => v.id !== videoId));
  };

  const clearFavorites = () => {
    setFavorites([]);
  };
  // ----------------

  const isFavorite = (videoId) => {
    return favorites.some((v) => v.id === videoId);
  };

  // Nh export thm 2 hm mi
  return { favorites, toggleFavorite, removeFavorite, clearFavorites, isFavorite };
};