import { useState, useEffect } from "react";
import { decode } from "@msgpack/msgpack";

export const useVideoManager = () => {
  const [files, setFiles] = useState([]);
  const [selectedFile, setSelectedFile] = useState("");
  const [videoIds, setVideoIds] = useState([]);
  const [loading, setLoading] = useState(false);

  // 1. Fetch File Index
  useEffect(() => {
    fetch("/json/msg/fileIndex.msgpack")
      .then((res) => res.arrayBuffer())
      .then((buffer) => {
        const data = decode(new Uint8Array(buffer));
        setFiles(data || []);
        
        // Restore last selected
        const lastSelected = localStorage.getItem("lastSelectedFile");
        if (lastSelected && data.includes(lastSelected)) {
          setSelectedFile(lastSelected);
        } else if (data.length > 0) {
          setSelectedFile(data[0]);
        }
      })
      .catch((err) => console.error("Error loading file index:", err));
  }, []);

  // 2. Fetch Video IDs when file changes
  useEffect(() => {
    if (!selectedFile) return;
    setLoading(true);
    
    fetch(`/json/msg/${selectedFile.replace(/\.json$/i, ".msgpack")}`)
      .then((res) => res.arrayBuffer())
      .then((buffer) => {
        const data = decode(new Uint8Array(buffer));
        setVideoIds(Array.isArray(data) ? data : []);
        setLoading(false);
        localStorage.setItem("lastSelectedFile", selectedFile);
      })
      .catch((err) => {
        console.error("Error fetching video IDs:", err);
        setLoading(false);
      });
  }, [selectedFile]);

  return { files, selectedFile, setSelectedFile, videoIds, loading };
};
