// src/components/pages/ContactUs.js
import "../assets/styles/StaticPages.css";
import { Link } from "react-router-dom";
import React from "react";
import ScrollToTopButton from "../components/common/ScrollToTopButton";

const ContactUs = () => {
  return (
    <div className="translations-content-container">
      <div className="container">
        <div className="home-button-container">
          <Link to="/" className="home-button">
            Home
          </Link>
        </div>

        {/* Scroll to Top Button */}
        <ScrollToTopButton />

        <div className="tab-content translations-content-item en visible">
          <h1>Contact Us</h1>
          <p>
            Wed love to hear from you! Whether you have questions, feedback, or
            suggestions, feel free to reach out to us using the information
            below.
          </p>

          <h2>Get in Touch</h2>
          <p>
            <strong>Email:</strong>{" "}
            <a href="mailto:dientu078@gmail.com">dientu078@gmail.com</a>
          </p>
          <p>
            <strong>Phone:</strong> +84 (38) 404-7890
          </p>
          <p>
            <strong>Address:</strong> 266 Doi Can, Ba Dinh, Ha Noi
          </p>

          <h2>Send Us a Message</h2>
          <form
            action="https://formspree.io/f/xgvvvokb"
            method="POST"
            className="contact-form"
          >
            <label htmlFor="name">Name:</label>
            <input type="text" id="name" name="name" placeholder="Your Name" required />

            <label htmlFor="email">Email:</label>
            <input
              type="email"
              id="email"
              name="email"
              placeholder="Your Email"
              required
            />

            <label htmlFor="message">Message:</label>
            <textarea
              id="message"
              name="message"
              rows="5"
              placeholder="Your Message"
              required
            ></textarea>

            <button type="submit">Send Message</button>
          </form>

          <h2>Follow Us</h2>
          <p>
            <a href="https://facebook.com/luanbka" target="_blank" rel="noopener noreferrer">
              Facebook
            </a>{" "}
            |{" "}
            <a href="https://www.twitter.com/zzzluan/" target="_blank" rel="noopener noreferrer">
              Twitter
            </a>{" "}
            |{" "}
            <a href="https://github.com/zthanhluan" target="_blank" rel="noopener noreferrer">
              GitHub
            </a>
          </p>
        </div>
      </div>
    </div>
  );
};

export default ContactUs;