// src/components/common/QRCodeModal.js
import React from "react";
import QRCode from "react-qr-code";
import "./QRCodeModal.css"; // Chng ta s thm CSS  bc sau

const QRCodeModal = ({ isOpen, onClose, videoId, currentTime }) => {
  if (!isOpen || !videoId) return null;

  // To link YouTube km thi gian (v d: ?t=120)
  const shareUrl = `https://youtu.be/${videoId}?t=${Math.floor(currentTime)}`;

  return (
    <div className="qr-overlay" onClick={onClose}>
      <div className="qr-content" onClick={(e) => e.stopPropagation()}>
        <div className="qr-header">
            <h3>Scan to Watch on Mobile</h3>
            <button className="qr-close-btn" onClick={onClose}></button>
        </div>
        
        <div className="qr-body">
            <div className="qr-code-wrapper">
                {/* QR Code cn nn trng  d qut */}
                <QRCode 
                    value={shareUrl} 
                    size={200} 
                    style={{ height: "auto", maxWidth: "100%", width: "100%" }}
                    viewBox={`0 0 256 256`}
                />
            </div>
            <p className="qr-instruction">
                Open Camera app on your phone and point at this code.
            </p>
            <div className="qr-link-preview">
                Timestamp: {new Date(currentTime * 1000).toISOString().substr(11, 8)}
            </div>
        </div>
      </div>
    </div>
  );
};

export default QRCodeModal;