// src/components/pages/AboutUs.js
import "../assets/styles/StaticPages.css";
import { Link } from "react-router-dom";
import React from "react";
import ScrollToTopButton from "../components/common/ScrollToTopButton";

const AboutUs = () => {
  return (
    <div className="translations-content-container">
      <div className="container">
        <div className="home-button-container">
          <Link to="/" className="home-button">
            Home
          </Link>
        </div>
        {/* Scroll to Top Button */}
        <ScrollToTopButton />
        <div className="tab-content translations-content-item en visible">
          <h1>About Us</h1>
          <p>Welcome to <strong>Random YouTube Video</strong>  a tool created with the goal of making learning English fun, easy, and distraction-free.</p>

          <p>Hi, Im the creator of this platform. I built this tool as a personal project to help myself learn English by watching YouTube videos on subjects I enjoy, all while working. What I wanted was a way to focus on learning without the interruptions of ads, and thus, this random video player was born.</p>

          <p>Its been a fantastic journey so far, allowing me to stay immersed in interesting topics while improving my language skills. Now, Im excited to share it with you.</p>

          <p>This randomizer is designed to make learning English simple and enjoyable. It offers a variety of curated videos tailored to your interests, so you can explore new subjects, expand your vocabulary, and build your language skills  all without distractions.</p>

          <p>When Im not working on projects like this, I enjoy diving into random memes, brainstorming creative ideas, and losing myself in YouTube rabbit holes. The world is full of surprises, and I believe we can learn a little English while we explore it together.</p>

          <p>Thanks for being here  lets discover something new and exciting!</p>
        </div>
      </div>
    </div>
  );
};

export default AboutUs;
