import React, { useState, useEffect } from "react";
import { get, set } from 'idb-keyval';
import { decode } from "@msgpack/msgpack";
import "./TranslateLibre.css";

const STORAGE_KEY = "translateLibreHistory";

// Cache object to store each chunk by first letter
const dictCache = {};

// Lazy-load and cache one chunk (e.g. a.json, b.json)
const fetchDictionaryChunk = async (firstChar) => {
  const key = firstChar.toLowerCase();

  // Return cached chunk if exists
  if (dictCache[key]) return dictCache[key];

  const cachedChunk = await get(key);
  if (cachedChunk) {
    dictCache[key] = cachedChunk;
    return cachedChunk;
  }

  try {
    const res = await fetch(`/dictionary_chunks/${key}.msgpack`);
    const buffer = await res.arrayBuffer();
    const chunk = decode(new Uint8Array(buffer));
    
    dictCache[key] = chunk; // Cache it in memory
    await set(key, chunk); // Save to IndexedDB
    return chunk;
  } catch (err) {
    console.error(` Failed to load chunk: ${key}.json`, err);
    dictCache[key] = {}; // Cache empty fallback to prevent retry loops
    return {};
  }
};

const TranslateLibre = () => {
  const [text, setText] = useState("");
  const [translated, setTranslated] = useState("");
  const [loading, setLoading] = useState(false);
  const [history, setHistory] = useState([]);

  useEffect(() => {
    const saved = localStorage.getItem(STORAGE_KEY);
    if (saved) {
      setHistory(JSON.parse(saved));
    }
  }, []);

  useEffect(() => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(history));
  }, [history]);

  const handleTranslate = async () => {
    const input = text
    .trim()
    .toLowerCase()
    .replace(/\s+/g, " "); // Normalize multiple spaces to single
    
    if (!input) return;

    setLoading(true);

    try {
      const words = input.split(" ");
      const resultLines = [];

      for (const word of words) {
        const firstChar = word[0];
        const dict = await fetchDictionaryChunk(firstChar);
        const definition = dict[word];
        if (definition) {
          resultLines.push(`${definition}`);
        } else {
          resultLines.push(``);
        }
      }

      const result = resultLines.join(" ");

      if (result) {
        setTranslated(result);
        setHistory((prev) => [
          { input: text, output: result },
          ...prev,
        ]);
        setText(""); // Clear input
      } else {
        setTranslated(" Not found in dictionary.");
      }
    } catch (err) {
      console.error("Translation error:", err);
      setTranslated(" Translation failed. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  const handleClearHistory = () => {
    setHistory([]);
    localStorage.removeItem(STORAGE_KEY);
  };

  return (
    <div className="translator-compact" style={{ flexDirection: "column", gap: 12 }}>
      <div style={{ display: "flex", gap: 8 }}>
        <input
          type="text"
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="English  Vietnamese"
          className="translator-input"
          onKeyDown={(e) => e.key === "Enter" && handleTranslate()}
        />
        <button onClick={handleTranslate} disabled={loading} className="translator-btn">
          {loading ? "..." : ""}
        </button>
      </div>

      {translated && (
        <div className="translator-output">
          <strong>Result:</strong> {translated}
        </div>
      )}

      {history.length > 0 && (
        <div className="translator-history">
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
            <h3>History</h3>
            <button onClick={handleClearHistory} className="translator-clear-btn" title="Clear history">
              
            </button>
          </div>

          <ul>
            {history.map((item, i) => (
              <li key={i} className="translator-history-item">
                <span className="translator-input-highlight">{item.input}</span>  {item.output}
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  );
};

export default TranslateLibre;
