// src/pages/Home.js
import React, { useEffect, useRef, useState } from "react";
import "../assets/styles/global.css"; 
import "../components/YouTubeApp.css";

// Components
import BuyMeACoffeeButton from '../components/common/BuyMeACoffeeButton';
import ScrollToTopButton from '../components/common/ScrollToTopButton';
import Content from '../components/features/HomeContent/Content';
import TranslateLibre from '../components/features/Dictionary/TranslateLibre';
import LibrarySidebar from '../components/features/Library/LibrarySidebar';
import HistorySidebar from '../components/features/History/HistorySidebar';
import QRCodeModal from '../components/common/QRCodeModal'; // <--- IMPORT MI

// Hooks
import { useVideoManager } from "../hooks/useVideoManager";
import { useVideoHistory } from "../hooks/useVideoHistory";
import { useDarkMode } from "../hooks/useDarkMode";
import { useFavorites } from "../hooks/useFavorites";
import { useWatchHistory } from "../hooks/useWatchHistory";
import { useSleepTimer } from "../hooks/useSleepTimer";

// ... (Gi nguyn component TopBar) ...
const TopBar = ({ files, selectedFile, onFileChange, onRandom, isMobile, isDarkMode, onToggleDarkMode, onOpenLibrary, onOpenHistory, timerState, onCycleTimer }) => {
    return (
       <div className="top-layout">
          <div className="search-container">
            <a href="/" className="logo-link"><img src="/icon.png" alt="Logo" className="logo" width="50" height="50"/></a>
            {!isMobile && <button className="random-button" onClick={onRandom}>Random</button>}
            {!isMobile && <label htmlFor="fileSelector" className="sunset-label">Subject:</label>}
            <select id="fileSelector" className="sunset-select" value={selectedFile} onChange={(e) => onFileChange(e.target.value)}>
              {files.map((f, i) => (<option key={i} value={f}>{f.replace("videoIds-", "").replace(".json", "")}</option>))}
            </select>
            <button className="sunset-select" style={{ width: 'auto', minWidth: 'auto', padding: '0 10px' }} onClick={onOpenHistory} title="Watch History"></button>
            <button className="sunset-select" style={{ width: 'auto', minWidth: 'auto', padding: '0 10px' }} onClick={onOpenLibrary} title="Library"></button>
            <button className="sunset-select" style={{ width: 'auto', minWidth: 'auto', padding: '0 10px', background: timerState.isActive ? '#ff4757' : undefined, color: timerState.isActive ? 'white' : undefined, fontWeight: 'bold' }} onClick={onCycleTimer} title="Sleep Timer">
                {timerState.isActive ? ` ${timerState.formatTime()}` : " Off"}
            </button>
            <input type="checkbox" className="checkbox" id="checkbox" checked={isDarkMode} onChange={onToggleDarkMode} />
            <label htmlFor="checkbox" className="checkbox-label"><i className="icon-moon"></i><i className="icon-sun"></i><span className="ball"></span></label>
          </div>
       </div>
    );
};


// --- CONTROL BAR ( thm nt QR) ---
const ControlBar = ({ 
    onPrev, onNext, canPrev, 
    isMuted, onToggleMute, 
    isFavorite, onToggleFavorite, 
    isZenMode, onToggleZenMode,
    loopStart, loopEnd, onSetA, onSetB, onClearLoop,
    onOpenQR,
    isDarkMode // <--- (Quan trng) Dng bin ny  check ch  sng/ti
}) => {

    // Hm helper: T ng chnh mu nt Loop cho d nhn
    const getLoopButtonStyle = (isActive) => {
        // Mc nh (Dark Mode)
        let bg = 'rgba(255,255,255,0.2)';
        let color = 'white';

        // Nu l Light Mode v nt KHNG active -> Chuyn sang mu xm, ch en
        if (!isDarkMode && !isActive) {
            bg = '#e0e0e0'; 
            color = '#333'; 
        }

        // Nu nt ang Active (ang bt Loop) -> Lun mu , ch trng
        if (isActive) {
            bg = '#ff4757';
            color = 'white';
        }

        return {
            width: '35px', height: '20px', padding: 0, fontSize: '10px', 
            borderRadius: '4px', border: 'none', fontWeight: 'bold', cursor: 'pointer',
            backgroundColor: bg,
            color: color
        };
    };

    return (
      <div className="button-container">
        <button className="button-custom previous-button" onClick={onPrev} disabled={!canPrev} title="Previous"><span className="iconsized"></span></button>
        <button className="button-custom like-button" onClick={onToggleMute} title={isMuted ? "Unmute" : "Mute"}><span className="iconsized">{isMuted ? "" : ""}</span></button>
        
        {/* Loop Controls: S dng hm style ng */}
        <div style={{ display: 'flex', flexDirection: 'column', gap: '2px', margin: '0 5px' }}>
            <button 
                className="button-custom" 
                onClick={onSetA} 
                title="Set Loop A" 
                style={getLoopButtonStyle(loopStart !== null)}
            >
                A
            </button>
            <button 
                className="button-custom" 
                onClick={loopEnd !== null ? onClearLoop : onSetB} 
                title="Set Loop B" 
                style={getLoopButtonStyle(loopEnd !== null)}
            >
                {loopEnd !== null ? "" : "B"}
            </button>
        </div>
    
        {/* Nt QR Share */}
        <button 
            className="button-custom" 
            onClick={onOpenQR} 
            title="Scan QR" 
            style={{ width: '40px', fontSize: '18px' }}
        >
            
        </button>
    
        <button className="button-custom like-button" onClick={onToggleFavorite} style={{ backgroundColor: isFavorite ? '#ff4757' : '#FF69B4' }}><span className="iconsized">{isFavorite ? "" : ""}</span></button>
        <button className="button-custom next-button" onClick={onToggleZenMode} title="Zen Mode" style={{ backgroundColor: isZenMode ? '#6c5ce7' : '#a29bfe' }}><span className="iconsized">{isZenMode ? "" : ""}</span></button>
        <button className="button-custom next-button" onClick={onNext} title="Next"><span className="iconsized"></span></button>
      </div>
    );
};

// --- MAIN COMPONENT ---
function Home({ isMobile }) {
    // Hooks
    const { files, selectedFile, setSelectedFile, videoIds, loading } = useVideoManager();
    const { currentVideoId, playRandomVideo, playSpecificVideo, goBack, goNext, canGoBack } = useVideoHistory(videoIds);
    const { isDarkMode, toggleDarkMode } = useDarkMode();
    const { favorites, toggleFavorite, isFavorite, removeFavorite, clearFavorites } = useFavorites();
    const { history, addToHistory, removeFromHistory: removeFromHistoryLog, clearHistory } = useWatchHistory();

    const playerRef = useRef(null);
    const [isMuted, setIsMuted] = useState(false);
    const [title, setTitle] = useState("");
    
    // UI States
    const [isLibraryOpen, setIsLibraryOpen] = useState(false);
    const [isHistoryOpen, setIsHistoryOpen] = useState(false);
    const [isZenMode, setIsZenMode] = useState(false);
    const [loopStart, setLoopStart] = useState(null);
    const [loopEnd, setLoopEnd] = useState(null);

    // --- QR STATES (Mi) ---
    const [isQROpen, setIsQROpen] = useState(false);
    const [qrTime, setQrTime] = useState(0);

    // Handlers
    const timerState = useSleepTimer(() => {
        if (playerRef.current && typeof playerRef.current.pauseVideo === 'function') playerRef.current.pauseVideo();
    });
    const handleCycleTimer = () => {
        if (!timerState.isActive) timerState.startTimer(15); 
        else {
            const currentMins = Math.ceil(timerState.timeLeft / 60);
            if (currentMins <= 15) timerState.startTimer(30);
            else if (currentMins <= 30) timerState.startTimer(60);
            else timerState.cancelTimer();
        }
    };

    const handleToggleMute = () => {
        const newMutedState = !isMuted;
        setIsMuted(newMutedState);
        if (playerRef.current) newMutedState ? playerRef.current.mute() : playerRef.current.unMute();
    };

    const handlePlayFromList = (videoId) => {
        const videoInfo = favorites.find(v => v.id === videoId) || history.find(v => v.id === videoId);
        if (videoInfo && videoInfo.category && videoInfo.category !== selectedFile) {
            playSpecificVideo(videoId, true);
            setSelectedFile(videoInfo.category);
        } else {
            playSpecificVideo(videoId, false);
        }
    };

    // --- QR HANDLER (Mi) ---
    const handleOpenQR = () => {
        if (playerRef.current && typeof playerRef.current.getCurrentTime === 'function') {
            const t = playerRef.current.getCurrentTime();
            setQrTime(t); // Ly thi gian hin ti
            setIsQROpen(true); // M Modal
            
            // T ng Pause video khi m QR  ngi dng ly in thoi
            playerRef.current.pauseVideo();
        }
    };

    // Loop handlers
    const handleSetA = () => { if (playerRef.current) { setLoopStart(playerRef.current.getCurrentTime()); setLoopEnd(null); } };
    const handleSetB = () => { if (playerRef.current) { const t = playerRef.current.getCurrentTime(); if (loopStart !== null && t > loopStart) { setLoopEnd(t); playerRef.current.seekTo(loopStart, true); } } };
    const handleClearLoop = () => { setLoopStart(null); setLoopEnd(null); };

    // Effects & Player Init (Gi nguyn nh c)
    useEffect(() => { if (isZenMode) { document.body.classList.add('zen-mode-active'); if (!isDarkMode) toggleDarkMode(); } else { document.body.classList.remove('zen-mode-active'); } }, [isZenMode, isDarkMode, toggleDarkMode]);
    useEffect(() => { if (title?.trim()) document.title = `${title}`; else document.title = "Random YouTube Video"; }, [title]);
    useEffect(() => { if (videoIds.length > 0) playRandomVideo(); }, [videoIds]);

    useEffect(() => {
        if (!currentVideoId) return;
        setTitle("Loading..."); 
        handleClearLoop();
        const loadYouTubeAPI = () => { if (!window.YT) { const script = document.createElement("script"); script.src = "https://www.youtube.com/iframe_api"; document.body.appendChild(script); window.onYouTubeIframeAPIReady = initializePlayer; } else { initializePlayer(); } };
        const initializePlayer = () => {
            if (playerRef.current) { try { playerRef.current.destroy(); } catch(e) {} }
            playerRef.current = new window.YT.Player("youtube-iframe-container", {
                videoId: currentVideoId,
                playerVars: { autoplay: 1, controls: 1, rel: 0, modestbranding: 1 },
                events: {
                    onReady: (event) => { if (isMuted) event.target.mute(); else event.target.unMute(); const videoData = event.target.getVideoData(); if (videoData && videoData.title) setTitle(videoData.title); },
                    onStateChange: (event) => {
                        if (event.data === window.YT.PlayerState.ENDED) goNext();
                        if (event.data === window.YT.PlayerState.PLAYING) { const videoData = event.target.getVideoData(); if (videoData && videoData.title) { addToHistory({ id: currentVideoId, title: videoData.title, category: selectedFile }); } }
                    }
                },
            });
        };
        loadYouTubeAPI();
    }, [currentVideoId]);

    useEffect(() => {
        if (!currentVideoId) return;
        const interval = setInterval(() => { if (playerRef.current && typeof playerRef.current.getCurrentTime === 'function') { try { const player = playerRef.current; const currentTime = player.getCurrentTime(); if (loopStart !== null && loopEnd !== null) { if (currentTime >= loopEnd) { player.seekTo(loopStart, true); return; } } if (loopEnd === null) { if (player.getPlayerState() === 1 && player.getDuration() > 0 && (player.getDuration() - currentTime < 1)) { goNext(); } } } catch (e) {} } }, 200); 
        return () => clearInterval(interval);
    }, [currentVideoId, goNext, loopStart, loopEnd]);

    useEffect(() => {
        const handleKeyDown = (e) => {
            if (["INPUT", "TEXTAREA"].includes(e.target.tagName)) return;
            if (e.code === "ArrowRight") goNext();
            if (e.code === "ArrowLeft") goBack();
            if (e.code === "Space") { e.preventDefault(); if (playerRef.current) playerRef.current.getPlayerState() === 1 ? playerRef.current.pauseVideo() : playerRef.current.playVideo(); }
            if (e.key === "[") handleSetA(); if (e.key === "]") handleSetB(); if (e.key === "\\") handleClearLoop();
        };
        window.addEventListener("keydown", handleKeyDown);
        return () => window.removeEventListener("keydown", handleKeyDown);
    }, [goNext, goBack, loopStart, loopEnd]);

    const wrapperClass = `main-layout ${isDarkMode ? 'dark-mode' : 'light-mode'} ${isZenMode ? 'zen-mode' : ''}`;

    return (
      <div className={wrapperClass}>
        {!isZenMode && <ScrollToTopButton />}
        <div className="blank-layout"></div>
        
        {!isZenMode && (
            <TopBar 
                files={files} selectedFile={selectedFile} onFileChange={setSelectedFile} onRandom={playRandomVideo}
                isMobile={isMobile} isDarkMode={isDarkMode} onToggleDarkMode={toggleDarkMode}
                onOpenLibrary={() => setIsLibraryOpen(true)} onOpenHistory={() => setIsHistoryOpen(true)}
                timerState={timerState} onCycleTimer={handleCycleTimer}
            />
        )}
        {isZenMode && timerState.isActive && <button className="zen-timer" onClick={handleCycleTimer}> {timerState.formatTime()}</button>}

        <div className="webview-layout">
          {loading ? ( <div className="loading-container"><div className="loading-spinner"></div><div>Loading...</div></div> ) : ( <div className="media-theme-sutro"> <div id="youtube-iframe-container" className="youtube-video" style={{ width: '100%', height: '100%' }}></div> </div> )}
        </div>

        <ControlBar 
            onPrev={goBack} onNext={goNext} onRandom={playRandomVideo} canPrev={canGoBack}
            isMuted={isMuted} onToggleMute={handleToggleMute}
            isFavorite={isFavorite(currentVideoId)} onToggleFavorite={() => toggleFavorite({ id: currentVideoId, title: title, category: selectedFile })}
            isZenMode={isZenMode} onToggleZenMode={() => setIsZenMode(!isZenMode)}
            loopStart={loopStart} loopEnd={loopEnd} onSetA={handleSetA} onSetB={handleSetB} onClearLoop={handleClearLoop}
            onOpenQR={handleOpenQR} // <--- Truyn handler vo ControlBar
        />
        
        {isMobile && !isZenMode && <button className="random-button" onClick={playRandomVideo}>Random</button>}

        {!isZenMode && (
            <>
                <TranslateLibre />
                <Content />
                {!isZenMode && <BuyMeACoffeeButton />}
                <div className="ad ad-left"></div>
                <div className="ad ad-right"></div>
            </>
        )}
        
        <LibrarySidebar isOpen={isLibraryOpen} onClose={() => setIsLibraryOpen(false)} favorites={favorites} onPlayVideo={handlePlayFromList} onRemove={removeFavorite} onClear={clearFavorites} />
        <HistorySidebar isOpen={isHistoryOpen} onClose={() => setIsHistoryOpen(false)} history={history} onPlayVideo={handlePlayFromList} onRemove={removeFromHistoryLog} onClear={clearHistory} />

        {/* --- QR MODAL --- */}
        <QRCodeModal 
            isOpen={isQROpen} 
            onClose={() => setIsQROpen(false)} 
            videoId={currentVideoId} 
            currentTime={qrTime} 
        />
        {/* ---------------- */}
      </div>
    );
}

export default Home;