"use client";

import useCanvasCursor from "./useCanvasCursor.js";

const CanvasCursor = () => {
  useCanvasCursor();

  const canvasStyle = {
    position: "fixed",
    inset: 0,
    pointerEvents: "none",
    zIndex: 2147483647, // Topmost priority
    background: "transparent", // No visual interference
  };

  return (
    <canvas
      className="pointer-events-none fixed inset-0"
      id="canvas"
      style={canvasStyle} // Apply inline styles
    />
  );
};

export default CanvasCursor;

