// src/components/features/Library/LibrarySidebar.js
import React from "react";
import "./LibrarySidebar.css";

// Thm props: onRemove, onClear
const LibrarySidebar = ({ isOpen, onClose, favorites, onPlayVideo, onRemove, onClear }) => {
  
  // Hm xc nhn trc khi xa tt c
  const handleClearAll = () => {
    if (favorites.length > 0) {
      if (window.confirm("Are you sure you want to delete all favorites?")) {
        onClear();
      }
    }
  };

  return (
    <>
      <div 
        className={`library-overlay ${isOpen ? "open" : ""}`} 
        onClick={onClose}
      ></div>

      <div className={`library-sidebar ${isOpen ? "open" : ""}`}>
        <div className="library-header">
            <div className="header-top">
                <h3>My Favorites ({favorites.length})</h3>
                <button onClick={onClose} className="close-btn"></button>
            </div>
            {/* Nt Clear All */}
            {favorites.length > 0 && (
                <button className="clear-all-btn" onClick={handleClearAll}>
                     Clear All
                </button>
            )}
        </div>
        
        <div className="library-content">
            {favorites.length === 0 ? (
                <div className="empty-msg">
                    <p>No favorite videos yet.</p>
                    <small>Click the  button to add videos here.</small>
                </div>
            ) : (
                <ul className="video-list">
                    {favorites.map((video) => (
                        <li key={video.id} className="video-item" onClick={() => {
                            onPlayVideo(video.id);
                            onClose(); // ng sidebar khi chn
                        }}>
                            <img 
                                src={`https://img.youtube.com/vi/${video.id}/mqdefault.jpg`} 
                                alt="thumb" 
                                className="video-thumb"
                            />
                            <div className="video-info">
                                <span className="video-title">{video.title || "No Title"}</span>
                                <span className="video-cat">{video.category?.replace("videoIds-", "").replace(".json", "") || "Unknown"}</span>
                            </div>
                            
                            {/* Nt Xa tng video */}
                            <button 
                                className="delete-item-btn" 
                                onClick={(e) => {
                                    e.stopPropagation(); // Chn click xuyn qua li
                                    onRemove(video.id);
                                }}
                                title="Remove"
                            >
                                
                            </button>
                        </li>
                    ))}
                </ul>
            )}
        </div>
      </div>
    </>
  );
};

export default LibrarySidebar;