// src/hooks/useVideoHistory.js
import { useState, useCallback, useEffect, useRef } from "react";

export const useVideoHistory = (videoIds) => {
  const [currentVideoId, setCurrentVideoId] = useState("");
  const [history, setHistory] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [seenVideos, setSeenVideos] = useState(new Set());

  // Ref  lu video ang ch c pht sau khi list video load xong
  const pendingVideoRef = useRef(null);

  // Khi danh sch videoIds thay i (do i file)
  useEffect(() => {
    // Nu c video ang ch (t Library), pht n ngay v khng reset lung tung
    if (pendingVideoRef.current) {
        const vid = pendingVideoRef.current;
        setCurrentVideoId(vid);
        setHistory([vid]);
        setCurrentIndex(0);
        setSeenVideos(new Set([vid]));
        
        pendingVideoRef.current = null; // Reset pending
    } else {
        // Logic c: Reset hon ton  auto-play random
        setHistory([]);
        setCurrentIndex(-1);
        setSeenVideos(new Set());
        setCurrentVideoId(""); 
    }
  }, [videoIds]);

  // Logic c
  const playRandomVideo = useCallback(() => {
    if (!videoIds || videoIds.length === 0) return;
    let candidates = videoIds.filter(id => !seenVideos.has(id));
    if (candidates.length === 0) {
      candidates = videoIds;
      setSeenVideos(new Set()); 
    }
    const randomIndex = Math.floor(Math.random() * candidates.length);
    const nextId = candidates[randomIndex];
    setCurrentVideoId(nextId);
    setHistory(prev => [...prev.slice(0, currentIndex + 1), nextId]);
    setCurrentIndex(prev => prev + 1);
    setSeenVideos(prev => new Set(prev).add(nextId));
  }, [videoIds, seenVideos, currentIndex]);

  const goBack = () => {
    if (currentIndex > 0) {
      setCurrentIndex(prev => prev - 1);
      setCurrentVideoId(history[currentIndex - 1]);
    }
  };

  const goNext = () => {
    if (currentIndex < history.length - 1) {
      setCurrentIndex(prev => prev + 1);
      setCurrentVideoId(history[currentIndex + 1]);
    } else {
      playRandomVideo();
    }
  };

  // Hm pht video c th (sa li  h tr set pending)
  const playSpecificVideo = (videoId, shouldPending = false) => {
    if (shouldPending) {
        // nh du  useEffect x l sau khi videoIds load xong
        pendingVideoRef.current = videoId;
    } else {
        // Pht ngay lp tc
        setCurrentVideoId(videoId);
        setHistory(prev => [...prev.slice(0, currentIndex + 1), videoId]);
        setCurrentIndex(prev => prev + 1);
    }
  };

  return { 
      currentVideoId, 
      playRandomVideo, 
      playSpecificVideo, 
      goBack, 
      goNext, 
      canGoBack: currentIndex > 0 
  };
};